import React from 'react';
import { Component } from 'react';
import { setIdToken, setAccessToken } from '../../utils/AuthService';

class Callback extends Component {

  constructor() {
    super()
  }

  componentDidMount() {
    console.log("hit");
    setAccessToken();
    setIdToken();
    window.location.href = "/";
  }

  render() {
    return null;
  }
}

export default Callback;


